import React, { useEffect, useRef } from 'react';
import './InvitationText.css';  // CSS  

const InvitationText = () => {
  const textRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          textRef.current.classList.add('show');  //     'show'  
        } else {
          textRef.current.classList.remove('show');  //     'show'  
        }
      },
      { threshold: 0.2 }  // 20%    
    );

    if (textRef.current) {
      observer.observe(textRef.current);  // ref    
    }

    return () => {
      if (textRef.current) {
        observer.unobserve(textRef.current);  //     
      }
    };
  }, []);

  return (
    <div
      ref={textRef}  // ref 
      className="invitation-text"
    >
      <h2>INVITATION</h2>
      <p>     </p>
      <p>   </p>
      <p>     </p>
      <p className="extra-space">      </p>
      <p>     </p>
      <div className="names">
        <p> .   </p>
        <p> .   </p>
      </div>
    </div>
  );
};

export default InvitationText;
